}
body,
html {
  height: 100%;
}
body,
html {
  direction: rtl;
  font-family: Scheherazade New, serif !important;
  font-size: 1.6vh !important;
  -webkit-user-select: none;
  -ms-user-select: none;
  user-select: none;
}
body {
  background-color: #535659;
  margin: 0;
}
* {
  box-sizing: border-box !important;
}
#root,
.page {
  height: 100%;
}
.page {
  background-color: #fff;
  border: 1rem solid #dc0000;
  box-shadow: 0 0 19px -4px #000;
  display: flex;
  flex-direction: column;
  margin: auto;
  max-width: 70.707vh;
}
.page .container {
  height: 100%;
  position: relative;
  width: 100%;
}
.teacher-btn {
  background: #fff;
  border: 0.3rem solid #333;
  border-radius: 50%;
  color: #333;
  cursor: pointer;
  font-family: Arial, sans-serif;
  font-size: 1.9rem;
  font-weight: 700;
  height: 2.5rem;
  left: 0.5rem;
  line-height: 1.9rem;
  position: absolute;
  text-align: center;
  top: 0.5rem;
  width: 2.5rem;
}
.teacher-btn:active,
.teacher-btn:hover {
  background-color: #e2e2e2;
}
.overlay {
  background-color: #32323269;
  height: 100%;
  left: 0;
  position: absolute;
  top: 0;
  width: 100%;
  z-index: 100;
}
.overlay .box {
  -webkit-animation: fade 1s ease forwards;
  animation: fade 1s ease forwards;
  background-color: #fff;
  box-shadow: 1px 1px 10px -4px #333;
  left: 50%;
  max-width: 50rem;
  padding: 1rem;
  position: absolute;
  text-align: center;
  top: 50%;
  -webkit-transform: translate(-50%, -50%);
  transform: translate(-50%, -50%);
  width: 90%;
}
.overlay .box h2 {
  font-size: 1.3rem;
}
.index-btn {
  background-color: #f7e561;
  border: 0.3rem solid #696969;
  border-radius: 50%;
  cursor: pointer;
  height: 2.7rem;
  position: absolute;
  right: 0.5rem;
  text-align: center;
  top: 0.5rem;
  width: 2.7rem;
  z-index: 1000;
}
.index-btn:active,
.index-btn:hover {
  background-color: #e7d548;
}
.index-btn span {
  background-color: #333;
  border-radius: 1rem;
  display: inline-block;
  height: 0.3rem;
  left: 0.36rem;
  position: absolute;
  top: 0.4rem;
  transition: 0.2s ease-in-out;
  width: 1.4rem;
}
.index-btn span:nth-child(2) {
  top: 0.85rem;
}
.index-btn span:nth-child(3) {
  top: 1.3rem;
}
.index-btn.open span:first-child,
.index-btn.open span:nth-child(2) {
  -webkit-transform: rotate(45deg);
  transform: rotate(45deg);
}
.index-btn.open span:first-child {
  top: 0.9rem;
}
.index-btn.open span:nth-child(3) {
  top: 0.9rem;
  -webkit-transform: rotate(-45deg);
  transform: rotate(-45deg);
}
.index-page {
  -webkit-animation: fade 0.5s ease forwards;
  animation: fade 0.5s ease forwards;
  background-color: rgb(236, 236, 236);

  height: 100%;
  margin: auto;
  max-width: 70.707vh;
}
.index-page .header h1 {
  font-size: 2rem;
  margin: 0;
  padding: 1rem;
  text-align: center;
}
.index-page .header h5 {
  margin: 0;
  font-size: 1rem;
  text-align: center;
}
.index-page .container {
  display: flex;
  flex-wrap: wrap;
  height: 87%;
  overflow-y: scroll;
  padding: 2rem 2rem 0 0;
}
.index-page .container .letter-box {
  height: 8rem;
  padding: 0 0 2rem 2rem;
  width: 33.333333%;
}
.index-page .container .letter-box .letter {
  background-color: #f7f7f7;
  border: 0.2rem solid #8b8b8b;
  border-radius: 1rem;
  box-shadow: 1px 1px 10px -4px #333;
  cursor: pointer;
  font-size: 1.2rem;
  font-weight: 700;
  height: 100%;
  line-height: 2rem;
  padding: 1rem 0;
  white-space: nowrap;
  position: relative;
  text-align: center;
}
.index-page .container .letter-box .letter:active,
.index-page .container .letter-box .letter:hover {
  background-color: #ccc;
}
.index-page .container .letter-box .letter .page-num {
  bottom: 0.5rem;
  color: #333;
  font-size: 1.1rem;
  left: 50%;
  transform: translateX(-50%);
  line-height: 1rem;
  position: absolute;
}
.index-page .container .letter-box:nth-child(-n + 6) .letter {
  border-color: #dc0000;
  color: #dc0000;
}
.index-page .container .letter-box:nth-child(n + 7):nth-child(-n + 28) .letter {
  border-color: #00983a;
  color: #00983a;
}
.index-page
  .container
  .letter-box:nth-child(n + 29):nth-child(-n + 33)
  .letter {
  border-color: #4a559e;
  color: #4a559e;
}
@-webkit-keyframes fade {
  0% {
    opacity: 0;
  }
  to {
    opacity: 1;
  }
}
@keyframes fade {
  0% {
    opacity: 0;
  }
  to {
    opacity: 1;
  }
}
.page-number {
  bottom: 0;
  margin: 0;
  position: absolute;
  text-align: center;
  width: 100%;
}
