.char-page {
  display: flex;
  flex: 1 1 auto;
  flex-direction: column;
  height: 95%;
  position: relative;
}
.char-page .row {
  position: relative;
  text-align: center;
}
.char-page .char-title {
  background-color: #f7f7f7;
  border: 0.2rem solid #fff18c;
  border-radius: 1rem;
  box-shadow: 1px 1px 10px -4px #333;
  cursor: pointer;
  display: inline-block;
  font-size: 2.1rem;
  font-weight: 700;
  margin: auto;
  padding: 0 1.3rem;
  text-align: center;
  -webkit-user-select: none;
  -ms-user-select: none;
  user-select: none;
}
.char-page .char-title:active {
  background-color: #e2e2e2;
}
.char-page .num {
  background-color: #dc0000;
  border: 0.3rem solid #bebebe;
  border-radius: 50%;
  color: #f1f1f1;
  display: inline-block;
  font-size: 1.7rem;
  font-weight: 700;
  height: 4.2rem;
  line-height: 3.5rem;
  margin-right: 1rem;
  text-align: center;
  width: 4.2rem;
}
.char-page .num.blue {
  background-color: #133459;
}
.char-page .words-container {
  display: flex;
  flex-direction: column;
  height: 85%;
  justify-content: space-evenly;
  padding-bottom: 1rem;
}
.char-page .word-container {
  align-items: stretch;
  display: flex;
  padding: 1rem;
}
.char-page .word-container .image {
  align-items: center;
  background-color: #fff;
  border: 0.3rem solid #fff18c;
  border-radius: 1rem;
  box-shadow: 1px 1px 10px -4px #333;
  display: flex;
  justify-content: center;
  margin-right: 1rem;
  position: relative;
  width: 7rem;
}
.char-page .word-container .image img {
  border-radius: inherit;
  width: 100%;
}
.char-page .word-container .image span.en-word {
  background-color: #fbf8d0;
  border: 0.2rem solid #fff18c;
  border-radius: 1rem;
  bottom: -1.8rem;
  font-size: 1.1rem;
  font-weight: 700;
  line-height: 1.4rem;
  position: absolute;
  text-align: center;
  width: 100%;
}
.char-page .word-container .word {
  align-items: center;
  align-self: center;
  align-self: stretch;
  background-color: #f7f7f7;
  border: 0.3rem solid #fff18c;
  border-radius: 2rem 10rem 10rem 2rem;
  box-shadow: 1px 1px 10px -4px #333;
  cursor: pointer;
  display: flex;
  flex: 1 1 auto;
  font-weight: 700;
  justify-content: center;
  position: relative;
  width: 80%;
}
.char-page .word-container .word:active {
  background-color: #e2e2e2;
}
.char-page .word-container .word .wrapper {
  flex: 1 1 auto;
  font-size: 2.6rem;
  position: relative;
  text-align: center;
}
.char-page .word-container .word .wrapper .actual-word {
  position: relative;
  z-index: 10;
}
.char-page .word-container .word .wrapper:after {
  border-top: 2px dashed #999;
  content: "";
  left: 50%;
  position: absolute;
  top: 60%;
  -webkit-transform: translateX(-50%);
  transform: translateX(-50%);
  width: 70%;
  z-index: 1;
}
.char-page .word-container .word .floating-letter {
  background: #f5efb0;
  border-radius: 50%;
  box-shadow: 1px 1px 10px -4px #333;
  font-size: 3rem;
  height: 6rem;
  position: relative;
  right: 0;
  text-align: center;
  width: 6rem;
}
.char-page .word-container .word .floating-letter span {
  left: 50%;
  position: absolute;
  top: 47%;
  -webkit-transform: translate(-50%, -50%);
  transform: translate(-50%, -50%);
}
.char-page .word-container .word .floating-letter:before {
  border-top: 2px dashed #999;
  content: "";
  left: 50%;
  position: absolute;
  top: 60%;
  -webkit-transform: translateX(-50%);
  transform: translateX(-50%);
  width: 50%;
}
.letters-page .note {
