}
.letters-page .image {
  margin: 1rem auto;
  position: relative;
  width: 80%;
}
.letters-page .image:after {
  content: "";
  height: 100%;
  left: 0;
  position: absolute;
  top: 0;
  width: 100%;
}
.letters-page .image img {
