.nav-button {
  background-color: #fcf4a6;
  border: 0.2rem solid #adadad;
  border-radius: 50%;
  bottom: 0.5rem;
  cursor: pointer;
  height: 2.5rem;
  position: absolute;
  width: 2.5rem;
  z-index: 10;
}
.nav-button:active .arrow:after,
.nav-button:active .arrow:before,
.nav-button:hover .arrow:after,
.nav-button:hover .arrow:before {
  background-color: #444;
}
.nav-button.right {
  right: 30%;
}
.nav-button.right .arrow:after,
.nav-button.right .arrow:before {
  left: 58%;
  top: 34%;
  -webkit-transform: translateX(-50%) rotate(55deg);
  transform: translateX(-50%) rotate(55deg);
}
.nav-button.right .arrow:after {
  top: 7%;
  -webkit-transform: translateX(-50%) rotate(-55deg);
  transform: translateX(-50%) rotate(-55deg);
}
.nav-button.left {
  left: 30%;
}
.nav-button.left .arrow:after,
.nav-button.left .arrow:before {
  left: 45%;
  top: 34%;
  -webkit-transform: translateX(-50%) rotate(-55deg);
  transform: translateX(-50%) rotate(-55deg);
}
.nav-button.left .arrow:after {
  top: 7%;
  -webkit-transform: translateX(-50%) rotate(55deg);
  transform: translateX(-50%) rotate(55deg);
}
.nav-button .arrow:after,
.nav-button .arrow:before {
  background: #7c7c7c;
  border-radius: 1rem;
  content: "";
  height: 60%;
  position: absolute;
  -webkit-transform: translateX(-50%) rotate(-55deg);
  transform: translateX(-50%) rotate(-55deg);
  width: 0.3rem;
}
